:root {
  --cubic: cubic-bezier(.4, 0, .2, 1);
}

.backdrop {
  width: 100%;
  height: 100%;
  transition: opacity 300ms var(--cubic), visibility 300ms var(--cubic);
  background-color: rgba(48, 48, 48, .6);
  position: fixed;
  top: 0;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.film__poster {
  overflow: hidden;
}

.film__poster img {
  object-fit: cover;
}

.backdrop.is-hidden .main-modal {
  transform: translate(-50%, -50%)scale(0);
}

.main-modal {
  transition: transform 300ms var(--cubic);
  max-width: 90%;
  max-height: 95%;
  background-color: #fff;
  margin-top: auto;
  margin-bottom: auto;
  padding: 48px 20px 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-x: hidden;
  overflow-y: auto;
  transform: translate(-50%, -50%);
}

