.backdrop-team {
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 1;
  transition: opacity 300ms var(--cubic), visibility 300ms var(--cubic);
  background-color: rgba(48, 48, 48, .6);
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.backdrop-team.is-hidden .main-modal {
  transform: translate(-50%, -50%)scale(0);
}

.btn-close-modal-team {
  width: 30px;
  height: 30px;
  color: #ff6b08;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #ff6b08;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.btn-close-modal-team:hover, .btn-close-modal-team:focus {
  animation: jello-diagonal-1 1s both;
}

@keyframes jello-diagonal-1 {
  0% {
    transform: skew(0deg 0deg);
  }

  30% {
    transform: skew(25deg 25deg);
  }

  40% {
    transform: skew(-15deg, -15deg);
  }

  50% {
    transform: skew(15deg, 15deg);
  }

  65% {
    transform: skew(-5deg, -5deg);
  }

  75% {
    transform: skew(5deg, 5deg);
  }

  100% {
    transform: skew(0deg 0deg);
  }
}

